<template>
  <section class="hero is-fullheight is-medium is-bold">
        <div class="hero-body">
          <div class="container">
            <div class="columns is-centered">
              <div class="column is-4">
              <article class="card is-rounded">
                <div class="card-content">
                  <h1 class="title">                                   
                    <img src="https://placeholdit.imgix.net/~text?txtsize=13&txt=150%C3%9750&w=150&h=50" alt="logo" width="200">
                  </h1>
                  <div class="field">
                    <p class="control">
                      <input class="input" type="email" v-model="username" placeholder="Email">
                      <i class="fa fa-envelope"></i>
                    </p>
                  </div>
                  <div class="field">
                    <p class="control">
                      <input class="input" type="password" v-model="password" placeholder="Password">
                      <i class="fa fa-lock"></i>
                    </p>
                  </div>
                  <div class="field">
                    <p class="control">
                      <label class="checkbox">
                        <input type="checkbox">
                        记住我
                      </label>
                    </p>
                  </div>
                  <div class="field">
                    <p class="control">
                      <button class="button is-primary is-medium is-fullwidth" @click="login">
                        <i class="fa fa-user"></i>
                        登录
                      </button><span v-if="loginfailed">登录失败</span>
                    </p>
                  </div>
                </div>
              </article>
              </div>
            </div>
          </div>
    </div>
  </section>
</template>

<script>
  import {login} from '../api/index'
  export default {
    data() {
      return {
          username:'araxi',
          password:'araxi',
          islogined: false,
          loginfailed: false,
          sessionToken: ''
      }
    },
    watch: {
    },
    methods: {
        login: function(){
            var that=this;
            login(that.username,that.password,res=>{
                if(res){
                    that.islogined=true;
                    that.sessionToken=res.sessionToken;
                    that.$router.push({path:'/admin/dashboard'});
                }
            })
        }
    },
    components: {
    },
    mounted(){
        //this.login();
    }
  };
</script>


